@import url(https://fonts.googleapis.com/css2?family=Caveat&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Pacifico&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html,
body {
  margin: 0 auto;
  height: 100vh;
  color: rgb(0, 0, 0);
  background-color: rgb(51, 51, 51);
  overflow: hidden;
}

.App {
  text-align: center;
  margin: 0 auto;
  height: 120vh;
  background: url(/tic-tac-react/static/media/desk.9057bc94.jpg);
  background-position: left;
  background-size: cover;
  position: relative;
}

table{border:none;max-width:500px;margin:0 auto;border-spacing:0px;border-collapse:separate}td{border:5px solid black;height:150px;width:150px;opacity:0}td:nth-child(3){border-left:5px solid black}td:nth-child(2){border-left:5px solid black}.board{text-align:center;box-sizing:border-box;display:flex;justify-content:center;flex-direction:column;background-image:url(/tic-tac-react/static/media/paper.5d535408.jpg);background-repeat:no-repeat;background-size:cover;padding:30px;font-family:"Caveat", cursive;max-width:650px;vertical-align:middle;height:100vh}.board .title{font-size:45px;color:#2e2e2e;font-weight:bold;margin-bottom:0px}.board .subtitle{margin-top:0;font-size:30px}.board-container{position:relative;display:flex;justify-content:center}.debug{display:none}.fade-in{opacity:0;animation-name:fadeInOpacity;animation-iteration-count:1;animation-timing-function:ease-in;animation-delay:0.25s;animation-duration:1s}@keyframes fadeInOpacity{0%{opacity:0}100%{opacity:1}}@media (max-width: 500px){.subtitle{margin:30px 0}.footer{margin-top:30px !important}td{width:80px;height:80px}}.row{display:flex}.column{flex:30% 1}.column:last-of-type{flex:70% 1}div.rodal-dialog{font-family:"Pacifico", cursive;color:#00972d}div.rodal-dialog h3{font-weight:bold;font-size:40px}div.rodal-dialog p{font-weight:400;font-size:25px}

.footer{font-size:25px;margin-top:5%}

